@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600&display=swap');

* {
    margin: 0;
    padding: 0;
    border: 0;
    outline: 0;
    box-sizing: border-box;
    list-style: none;
    text-decoration: none;
}

:root {
    --color-bg: #F0F0F0; /* Light grey for background */
    --color-bg-variant: #E0E0E0; /* Slightly darker shade for contrast */
    --color-primary: #20B2AA; /* lightseagreen for primary text and elements, used sparingly */
    --color-primary-variant: #3DCCC7; /* A lighter variant for accents */
    --color-white: #385170; /* Pure white for backgrounds and accents */
    --color-light: #142d4c; /* Light white for overlays and subtle accents */
    --color-dark: #333; /* Darker text or elements for better readability */

    --transition: all 400ms ease;

    --container-width-1g: 75%;
    --container-width-md: 86%;
    --container-width-sm: 90%;
}
html {
    scroll-behavior: smooth;
}

::-webkit-scrollbar {
    display: none;
}

body {
    font-family: 'Poppins', sans-serif;
    background: var(--color-bg);
    color: var(--color-dark); /* Dark color for main text to ensure readability */
    line-height: 1.7;
}

/* General Styles */

.container {
    width: var(--container-width-1g);
    margin: 0 auto;
}

h1, h2, h3, h4, h5 {
    font-weight: 500;
}

h1 {
    font-size: 2.5rem;
}

section {
    margin-top: 8rem;
}

section > h2,
section > h5 {
    text-align: center;
    color: var(--color-light);
}

section > h2 {
    color: var(--color-primary);
    margin-bottom: 3rem;
}

.text-light {
    color: var(--color-light);
}

a {
    color: var(--color-primary);
    transition: var(--transition);
}

a:hover {
    color: var(--color-white)
}

.btn {
    width: max-content;
    display: inline-block;
    color: var(--color-primary);
    padding: 0.75rem 1.2rem;
    border-radius: 0.4rem;
    cursor: pointer;
    border: 1px solid var(--color-primary);
    transition: var(--transition);
}

.bth:hover {
    background: var(--color-white);
    color: var(--color-bg);
    border-color: transparent;
}

.btn-primary {
    background: var(--color-primary);
    color: var(--color-bg);
}

img {
    display: block;
    width: 100%;
    object-fit: cover;
}

/* Media Queries (tablets) */

@media screen and (max-width: 1024px) {
    .container {
        width: var(--container-width-md);
    }    

    section{
        margin-top: 6rem;
    }
}

/* Media Queries (mobile phones) */
@media screen and (max-width: 600px) {
    .container {
        width: var(--container-width-sm);
    }    

    section{
        margin-top: 2rem;
    }
}
